body {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.app {
    text-align: center;
    color: #222223;
}

.app-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}


